import React from 'react'
import './pagination.scss'

const Pagination = (props) => {


    if (props.show) {
        return (
            <nav >
                <ul className="pagination">

                    <li className="page-item">
                        <button
                            onClick={() => {
                                props.prevPage()
                            }}>Prev</button>
                    </li>
                    <li className="page-item">
                        <button
                            onClick={() => {
                                props.nextPage()
                            }}>Next</button>
                    </li>
                </ul>
            </nav>
        )
    } else {
        return null
    }


}
export default Pagination