import React, { Component } from 'react'
import './movie_list.scss'
import MovieItem from '../movie_item/movie_item'

export default class MovieList extends Component {


    render() {

        if (this.props.show) {
            return (
                <React.Fragment>
                    <div className="movie_list">
                        <div className="wr">
                            <p className="movie_list__title">{this.props.title}</p>
                            <div className="movie_list__content">

                                {this.props.movieList.map((movie, key) =>
                                    <MovieItem
                                        key={key}
                                        title={movie.title}
                                        imageUrl={movie.poster_path}
                                        overview={movie.overview} />)
                                }
                            </div>
                        </div>
                    </div>
                </React.Fragment>
            )
        } else {
            return null
        }


    }
}