import React, { Component } from 'react'
import './filters.scss'

class Filters extends Component {

    state = {
        adult: false,
        year: ''
    }

    handleYear = (event) => {
        this.setState({
            year: event.target.value
        }, () => {

            this.props.yearHandler(this.state.year)
            console.log(this.state.year)
        })
    }

    render() {
        if (this.props.show) {

            return (
                <div className="filters">
                    <div className="wr">
                        <div className="filters__content">
                            <label >
                                <span>year</span>
                                <input
                                    value={this.state.year}
                                    type="number"
                                    onChange={(event) => {
                                        this.handleYear(event)
                                    }} />
                            </label>
                            <label >
                                <span>Adult</span>
                                <input
                                    type="checkbox"
                                    defaultChecked={this.state.adult}
                                    onChange={() => {
                                        this.setState({
                                            adult: !this.state.adult
                                        }, () => {
                                            this.props.adultHandler(this.state.adult)
                                        })
                                    }} />
                            </label>
                        </div>
                    </div>
                </div>
            )
        } else {
            return null
        }
    }

}

export default Filters