import React, { Component } from 'react'
import './movie_item.scss'

export default class MovieItem extends Component {

    render() {

        return (
            <React.Fragment>
                <div className="movie_item">
                    <div className="img_wr">
                        <img src={`https://image.tmdb.org/t/p/w500/${this.props.imageUrl}`} alt="" />
                    </div>

                    <span className="movie_item__title">{this.props.title}</span>
                    <p className="movie_item__overview">{this.props.overview}</p>
                </div>
            </React.Fragment>
        )
    }
}